body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    background-color: #e9c7b1;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

h1 {
    width: 100%;
    text-align: center;
    font-family: "Lucida Handwriting", "Courier New", monospace;
    color: white;
    font-size: 4rem;
}
.date {
    font-size: 300%;
}
.card {
    height: auto;
    padding: 14px;
    background: white;
    width: 80%;
    margin: auto;
    border: 1px solid black;
    border-radius: 20px;
}

.main {
    padding-top: 45%;
}

.first {
    background: #c88769;
    width: 50%;
    border-radius: 50%;
    text-align: center;
    color: white;
    font-weight: 700;
    vertical-align: middle;
    justify-content: center;
    align-items: center;
    margin: auto;
    font-size: 150%;
    display: flex;
    margin-block: 5%;
    padding: 20px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
    font-family: "Lucida Handwriting", "Courier New", monospace;
}
.first:after {
    content: "";
    display: block;
    padding-bottom: 100%;
}
.second {
    background: #e9b697;
    width: 50%;
    padding: 20px;
    border-radius: 50%;
    text-align: center;
    color: white;
    font-weight: 700;
    vertical-align: middle;
    justify-content: center;
    align-items: center;
    font-size: 150%;
    display: flex;
    margin: auto;
    margin-top: 10%;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.516);
    font-family: "Lucida Handwriting", "Courier New", monospace;
}

.second:after {
    content: "";
    display: block;
    padding-bottom: 100%;
}

.start {
    background-color: #c88769;
    width: 30%;
    padding-block: 10%;
    margin: auto;
    border-radius: 50%;
    text-align: center;
    color: white;
    max-height: none;
    font-size: 150%;
    font-family: "Lucida Handwriting", "Courier New", monospace;
    display: flex;
    align-items: center;
    justify-content: center;
}

.event {
    margin: auto;
    color: white;
    width: 80%;
    font-size: 20pt;
    background-color: #e9b697;
    line-height: 150%;
    padding-inline: 2%;
}

.day {
    margin: auto;
    margin-block: 7%;
    background-color: #c88769;
    width: 75%;
}

.day2 {
    margin: auto;
    margin-bottom: 7%;
    margin-top: 4%;
    background-color: #c88769;
    width: 75%;
}

.schedule {
  margin: auto;
  margin-top: 10%;
    text-align: center;
    width: 40%;
    height: 40%;
    background-color: #e9b697;
    font-size: 20pt;
    font-family:  "Courier New";
    color: white;
    font-weight: 750;
    padding: 4px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
}

.now {
    color: white;
    background: #c88769;
    width: 60%;
    border-radius: 50%;
    text-align: center;
    color: white;
    font-weight: 700;
    vertical-align: middle;
    justify-content: center;
    align-items: center;
    margin: auto;
    font-size: 150%;
    display: flex;
    margin-top: -15%;
    margin-left: 20%;

    padding: 20px;
}
.now:after {
    content: "";
    display: block;
    padding-bottom: 100%;
}

.next {
    color: white;
    background: #e9b697;
    width: 45%;
    border-radius: 50%;
    text-align: center;
    color: white;
    font-weight: 700;
    vertical-align: middle;
    justify-content: center;
    align-items: center;
    font-size: 150%;
    display: flex;
    margin-top: 10%;
    margin-left: 5%;
    padding: 20px;
}
.next:after {
    content: "";
    display: block;
    padding-bottom: 100%;
}
.titulotime{
  margin:auto;
  text-align: center;
  margin-top: 10%;
  color:#c88769;
  font-family: "Courier New";
  width:100%;
  font-size: 15pt;
  font-weight: 1000;
}
.countdowntime{
  margin:auto;
  text-align: center;
  margin-top: 1%;
  color:white;
  font-family: "Courier New";
  width:100%;
  font-size: 20pt;
  font-weight: 1000;
}